<template>
    <div class="userinfo-page">
       <div class="userinfo-box">
           <img :src="userInfo.avatar" 
           class="avatar" alt="">
           <el-upload
            :headers="headers"
            ref='upload'
            class="upload-demo"
            name="avatar"
            :data="withData"
            :on-change="getFile"
            action="http://8.135.61.132/cts/upload"
            :limit="1"
            :file-list="fileList">
            <el-button size="small" type="primary">更改头像</el-button>
            </el-upload>
            <el-input v-model="newPassword" v-show="showNew"></el-input>  
            <el-button size="small" type="success" @click="changePassword">更改密码</el-button>
           <div class="user-id">用户ID:{{userInfo.userId}}</div>
                  
           <!-- <div class="user-password">用户密码:123456</div> -->
       </div>
    </div>
</template>

<script>
import {updateUserPassword} from '../../network/loginAndRegist.js'
export default {
    name:'UserInfo',
    data(){
        return{
            fileList:[],
            withData:{
                userId:''
            },
            userInfo:{
            },
            newPassword:'',
            showNew:false,
            headers:{
                Authorization:null
            }
        }
    },
    methods:{
        changeAvatar()
        {
            console.log('改变头像');
        },
        getFile(file,fileList)
        {
            // console.log(fileList);
            if(file.response)
            {
                  this.userInfo.avatar=`http://${file.response.data.url}`;
                  console.log("上传后",file.response);
                  let userInfo=JSON.parse(localStorage.getItem('userInfo'));
                  userInfo.avatar=this.userInfo.avatar;
                  localStorage.setItem('userInfo',JSON.stringify(userInfo));
                  this.$refs['upload'].clearFiles();
                  this.$message({
                      message:'修改头像成功',
                      type:'success'
                  })
                  this.$router.go(0);
            }
           
        },
        getUserInfo(){
           let userInfo=JSON.parse(localStorage.getItem('userInfo'));
           this.userInfo=userInfo;
           this.withData.userId=this.userInfo.userId;
           this.headers.Authorization=userInfo.token;
        },
        async changePassword()
        {
             if(this.showNew)
            {
                if(this.newPassword.trim()=='')
                {
                    this.$message({
                        type:'warning',
                        message:'密码不能为空'
                    })
                    return;
                }
                const data={
                    userId:this.userInfo.userId,
                    password:this.newPassword
                }
                let res= await updateUserPassword(data);
                console.log(res);
                this.$message({
                    type:'success',
                    message:'修改密码成功！'
                })
                this.showNew=false;
            }
            else
            this.showNew=true;           
        }
    },
    created(){
        this.getUserInfo();
    }
}
</script>

<style scoped>
    #userinfo-page
    {
        background-color: #fff;
        opacity: 0.4;
        width: 100%;
        height: 100%;
        position:relative;
    }
    
    .avatar
    {
        width: 300px;
        height: 300px;
        border-radius: 50%;
        cursor: pointer;
    }
    .userinfo-box
    {
        position: absolute;
        text-align: center;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
    }

    .user-id,.user-password
    {
        color:#999;
        line-height: 50px;
        font-size: 40px;
    }
</style>